import React, { Component } from 'react';
import Section from '../frame/section';
import { ListGroup } from 'react-bootstrap';
import { FirestoreCollection } from "@react-firebase/firestore";
import { Star, Book } from "react-bootstrap-icons";
import Skeleton from 'react-loading-skeleton';

export default class MyCourses extends Component {

  retrieveCourseList = () => {
    return (
      <FirestoreCollection path="/courses/" orderBy={[{ field: "index", type: "desc" }]}>
        {courses => {
          return courses.isLoading ?
          <Skeleton count={10} className="mb-3"/>
          : this.courseList(courses);
        }}
      </FirestoreCollection>
    );
  }

  courseList = (courses) => {
    return (
      <ListGroup variant="flush">
        {
          courses.value && courses.value.map((course, index) => {
            return(
              <ListGroup.Item key={index} className="bg-transparent px-0"><Star className="mb-1" /> 
                &nbsp;{course.code} <i>{course.title}</i>
              </ListGroup.Item>
            );
          })
        }
      </ListGroup>
    );
  }

  render() {
    return(
      <Section id="mycourses" name="Courses" icon={<Book className="mr-1 mb-1"/>} opacity="0.3">
        {this.retrieveCourseList()}
      </Section>
    );
  }
}