import React, { Component } from 'react';
import Section from '../frame/section';
import {Table, Button} from 'react-bootstrap';
import { FirestoreCollection } from "@react-firebase/firestore";
import { Laptop } from "react-bootstrap-icons";
import Skeleton from 'react-loading-skeleton';

export default class MyProject extends Component {

  retrieveProjectTable = () => {
    return (
      <FirestoreCollection path="/projects/" orderBy={[{ field: "index", type: "desc" }]}>
        {projects => {
          return projects.isLoading ? 
          <Skeleton count={12} className="mb-3"/>
          : (
            window.matchMedia('(max-width: 768px)').matches === true
            ? this.largeProjectTable(projects.value)
            : this.shrinkedProjectTable(projects.value)
          )
        }}
      </FirestoreCollection>
    );
  }
  
  largeProjectTable = (projects) => {
    return (
      <Table striped bordered variant="light" className="w-100 p-0 m-0" style={{background: 'rgba(255, 255, 255, 0.6)'}}>
        <thead>
          <tr><th style={{width: 200}}>
            Name,<br/>
            Platform,<br />
            Description, Url
          </th></tr>
        </thead>
        <tbody>
          {projects.map((project, index) => {
            return(
              <tr key={index}><td>
                {project.name}<br/>
                {project.platform}
                <p style={{textAlign: 'left', color: 'grey', fontStyle: 'italic'}}>developed using {project.tool}</p>
                {project.description}<br/>
                <p className="text-right mt-3 mb-0">
                  {project.url!=='' && 
                    <a href={project.url} target="_blank" rel="noreferrer" className="ml-auto text-decoration-none text-light">
                      <Button variant="secondary" size="sm">{project.repository}
                      </Button>
                    </a>
                  }
                </p>
              </td></tr>
            );
          })}
        </tbody>
      </Table>
    );
  }

  shrinkedProjectTable = (projects) => {
    return (
      <Table striped bordered variant="light" className="w-100 p-0 m-0" style={{background: 'rgba(255, 255, 255, 0.6)'}}>
        <thead>
          <tr>
            <th style={{width: 200}}>Name</th>
            <th style={{width: 220}}>Platform</th>
            <th>Description</th>
            <th>Url</th>
          </tr>
        </thead>
        <tbody>
          {projects.map((project, index) => {
              return(
                <tr key={index}>
                  <td className="text-start">{project.name}</td>
                  <td>{project.platform}
                      <p className="text-start" style={{color: 'grey', fontStyle: 'italic'}}>developed using {project.tool}</p>
                  </td>
                  <td>{project.description}</td>
                  <td>{project.url!=='' && <a href={project.url} target="_blank" rel="noreferrer" className="text-decoration-none text-light">
                    <Button variant="dark" size="sm">{project.repository}</Button></a>}
                  </td>
                </tr>
              );
            })}
        </tbody>
      </Table>
    );
  }

  render() {
    return(
      <Section id="myprojects" name="Projects" icon={<Laptop className="mr-1 mb-1"/>} opacity="0.3">
        {this.retrieveProjectTable()}
      </Section>
    );
  }
}