import React, { Component } from 'react';
import { Container, Row, Form, Button, Modal } from 'react-bootstrap';
import { authUser, loginViaEmailPassword, logout } from './emailPasswordAuth';
import { retrieveCollectionData, updateCollectionData } from './dbController';

export default class Main extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isAuth: undefined,
      selectedCollection: "",
      collectionData: "",
      loginEmail: "",
      loginPassword: "",
      isMessageShown: false,
      modalMessage: {title: "", body: [], variant: "secondary"}
    }
  }

  componentDidMount() {
    this.accessAuthentication();
  }

  componentWillUnmount() {
    logout();
  }

  accessAuthentication = async () => {
    this.setState({
      isAuth: await authUser()
    });
  }

  loginUser = async () => {
    loginViaEmailPassword(this.state.loginEmail, this.state.loginPassword)
    .then(() => {
      this.setState({
        isAuth: true
      });
    })
    .catch((err) => {
      alert(err);
    })
    .finally(() => {
      this.setState({
        loginEmail: "",
        loginPassword: ""
      })
    });
  }

  handleCollection = (e) => {
    this.setState({selectedCollection: e.target.value}, async () => {
      await this.loadCollection();
    });
  }

  updateCollection = async (e) => {
    e.preventDefault();
    let backup = await retrieveCollectionData(this.state.selectedCollection);
    try {
      await updateCollectionData(this.state.selectedCollection, JSON.parse(this.state.collectionData))
      this.setState({
        modalMessage: {
          title: "Collection update success",
          body: ["The collection has been updated succesfully."],
          variant: "primary"
        }
      });
    }
    catch (err) {
      await updateCollectionData(this.state.selectedCollection, backup);
      this.setState({
        modalMessage: {
          title: "Collection update fail",
          body: ["Error occurs during the collection update", err.message],
          variant: "danger"
        }
      });
    }
    finally {
      this.setState({isMessageShown: true});
    };
  }

  loadCollection = async () => {
    if (this.state.selectedCollection) {
      let data = await retrieveCollectionData(this.state.selectedCollection);
      let replacer = data.length > 0 ? Object.keys(data[0]).sort() : null;
      this.setState({
        collectionData: JSON.stringify(data, replacer, 4)
      });
    }
    else {
      this.setState({
        collectionData: ""
      });
    }
  }

  render() {
    return (
      <Container fluid id="admin" className="overflow-auto px-md-5 px-3 pt-1 pb-3 vh-100 d-flex flex-column">
      {this.state.isAuth ? (
        <>
        <Row className="w-100 mx-0 mb-2 p-0 fs-1">Admin Page</Row>
        <Row className="w-100 mx-0 p-0 h-100 flex-grow-1">
          <Form className="m-0 p-0 h-100 d-flex flex-column" onSubmit={this.updateCollection}>
            <Form.Group controlId="selectedCollection">
              <Form.Control as="select" value={this.state.selectedCollection} onChange={this.handleCollection}>
                <option value="">Update...</option>
                <option value="introduction">Background</option>
                <option value="achievements">Achievements</option>
                <option value="projects">Projects</option>
                <option value="courses">Courses</option>
              </Form.Control>
              <Button variant="warning" className="mt-1" type="button" onClick={this.loadCollection}>Undo</Button>
              <Button variant="success" className="mt-1 mx-2" type="submit">Update</Button>
            </Form.Group>
            <Form.Group controlId="collectionData" className="mt-3 h-100 flex-grow-1 d-flex flex-column">
              <Form.Label>data in JSON</Form.Label>
              <Form.Control 
                as="textarea"
                disabled={this.state.selectedCollection === ""}
                value={this.state.collectionData} 
                onChange={(e) => {this.setState({collectionData: e.target.value})}}
                className="flex-grow-1"
              />
            </Form.Group>
          </Form>
        </Row>
        </>
      ) 
      : (
        <Modal.Dialog centered className="w-100">
          <Modal.Header>
            <Modal.Title>Admin Page Access</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form>
              <Form.Group controlId="loginEmail" className="mt-3">
                <Form.Label>Email</Form.Label>
                <Form.Control 
                  as="input" 
                  value={this.state.loginEmail} 
                  onChange={(e) => {this.setState({loginEmail: e.target.value})}}
                />
              </Form.Group>
              <Form.Group controlId="loginPassword" className="mt-3">
                <Form.Label>Password</Form.Label>
                <Form.Control 
                  as="input" 
                  type="password"
                  value={this.state.loginPassword} 
                  onChange={(e) => {this.setState({loginPassword: e.target.value})}}
                />
              </Form.Group>
            </Form>
          </Modal.Body>

          <Modal.Footer>
            <Button variant="success" onClick={this.loginUser}>Submit</Button>
          </Modal.Footer>
        </Modal.Dialog>
      )}
        <Modal centered show={this.state.isMessageShown}>
          <Modal.Header>
            <Modal.Title>{this.state.modalMessage.title}</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            {this.state.modalMessage.body.length > 0 && this.state.modalMessage.body.map((paragraph, index) => {
              return <p key={index}>{paragraph}</p>
            })}
          </Modal.Body>

          <Modal.Footer>
            <Button variant={this.state.modalMessage.variant} onClick={()=>{this.setState({isMessageShown: false})}}>OK</Button>
          </Modal.Footer>
        </Modal>
      </Container>
    );
  }
}