import React, { Component } from 'react';
import Section from '../frame/section';
import { Table } from 'react-bootstrap';
import { FirestoreCollection } from "@react-firebase/firestore";
import { Trophy } from 'react-bootstrap-icons';
import Skeleton from 'react-loading-skeleton';

export default class MyAchievements extends Component {

  retrieveAchievementTable = () => {
    return (
      <FirestoreCollection path="/achievements/" orderBy={[{ field: "index", type: "desc" }]}>
        {achievements => {
          return achievements.isLoading ? 
          <Skeleton count={12} className="mb-3"/>
          : (
            window.matchMedia('(max-width: 768px)').matches === true
            ? this.largeAchievementTable(achievements.value)
            : this.shrinkedAchievementTable(achievements.value)
          )
        }}
      </FirestoreCollection>
    );
  }

  largeAchievementTable = (achievements) => {
    return (
      <Table striped bordered variant="light" style={{background: 'rgba(255, 255, 255, 0.6)'}}>
        <thead>
          <tr><th style={{width: 200}}>
            Year,<br/>
            Event,<br />
            Organization
          </th></tr>
        </thead>
        <tbody>
          {achievements.map((achievement, index) => {
            return(
              <tr key={index}><td>
                {achievement.year}<br/>
                {achievement.event}<br/>
                <i>{achievement.organization}</i><br/>
              </td></tr>
            );
          })}
        </tbody>
      </Table>
    );
  }

  shrinkedAchievementTable = (achievements) => {
    return (
      <Table striped bordered variant="light" style={{background: 'rgba(255, 255, 255, 0.6)'}}>
        <thead>
          <tr>
            <th style={{width: '10%'}}>Year</th>
            <th style={{width: '60%'}}>Event</th>
            <th style={{width: '30%'}}>Organization</th>
          </tr>
        </thead>
        <tbody>
          {achievements.map((achievement, index) => {
            return(
              <tr key={index}>
                <td>{achievement.year}</td>
                <td>{achievement.event}</td>
                <td>{achievement.organization}</td>
              </tr>
            );
          })}
        </tbody>
      </Table>
    );
  }

  render() {
    return(
      <Section id="myachievements" name="Achievements" icon={<Trophy className="mr-1 mb-1"/>} opacity="0.3">
        {this.retrieveAchievementTable()}
        
      </Section>
    );
  }
}