import { Row, Navbar, Nav, NavDropdown } from 'react-bootstrap';
import AppNav from '../../frame/nav';
import { NAV_CONFIG } from '../../../../config/config';

export default class HorizontalNav extends AppNav {
  constructor(props) {
    super(props);
    this.state = {
      expand: false
    }
  }

  collapseMenuAndScrollTo = (section) => {
    this.setState({ expand: false });
    this.ScrollToSection(section);
  }

  render() {
    return(
      <Row id="horizontalNav" className={`w-100 m-0 ${this.props.show ? "horizontalNav-show" : "horizontalNav-hide"}`}>
        <Navbar 
          expand="sm" bg="light" variant="light"
          expanded={this.state.expand}
          onToggle={() => {this.setState({expand: !this.state.expand})} }
          className="px-3 py-1 w-100"
        >
          <Navbar.Brand onClick={() => this.collapseMenuAndScrollTo('title')} style={{cursor: 'pointer'}}>Ryan's Page</Navbar.Brand>
          <Navbar.Toggle aria-controls="horizontalNav-control" />
          <Navbar.Collapse id="horizontalNav-control" className="py-2">
            <Nav className="mr-auto">
              <NavDropdown.Divider />
              {
                NAV_CONFIG.components.map((component, index) => {
                  return (
                    <Nav.Link key={index} onClick={() => this.collapseMenuAndScrollTo(component.sectionID)}>
                      {component.name}
                    </Nav.Link>
                  );
                })
              }
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      </Row>
    );
  }
}