import { db } from '../../service/firebase';
import { authUser } from './emailPasswordAuth';

export const retrieveCollectionData = async (collection) => {
  try {
    if ((await authUser()) === false) {
      throw new Error("Unauthorized access");
    }
    let res = await db.collection(collection).get();
    let data = res.docs.map(doc => doc.data());
    data.sort((a, b) => {
      let x = a["index"]; let y = b["index"];
      return ((x > y) ? -1 : ((x < y) ? 1 : 0));
    });
    for (let i in data) {
      delete data[i].index;
    }
    return data;
  }
  catch(err) {
    throw new Error(err.message);
  }
}

export const updateCollectionData = async (collection, data) => {
  try {
    if ((await authUser()) === false) {
      throw new Error("Unauthorized access");
    }
    let res = await db.collection(collection).get();
    res.forEach(async (element) => {
      await element.ref.delete();
    });

    // Add phase
    data.forEach(async (obj, index) => {
      await db.collection(collection).add({
        ...obj, 
        index: data.length - index
      })
    });
    return await retrieveCollectionData(collection);
  }
  catch(err) {
    throw new Error(err.message);
  }
}