import React, { Component } from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import Header from './components/header';
import MyBackground from './components/background';
import MyProject from './components/projects';
import MyAchievements from './components/achievements';
import MyCourses from './components/courses';
import MyContact from  './components/contact';
import VerticalNav from './components/NavBars/verticalNav';
import HorizontalNav from './components/NavBars/horizontalNav';

export default class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      inSmallScreen: undefined,
      showTopNav: false
    }
  }

  componentDidMount() {
    this.setState({
      inSmallScreen: window.matchMedia('(max-width: 575px)').matches,
    }, () => {
      if (this.state.inSmallScreen) {
        let mainNavBottomOffset;
        window.addEventListener("scroll", () => {
          mainNavBottomOffset = document.getElementById('mainNav').offsetHeight + document.getElementById('mainNav').offsetTop;
          if (window.scrollY > mainNavBottomOffset && !this.state.showTopNav) {
            this.setState({showTopNav: true});
          }
          else if (window.scrollY <= mainNavBottomOffset && this.state.showTopNav) {
            this.setState({showTopNav: false});
          }
        });
      }
    });
  }

  render() {
    return(
      <Container fluid id="main" className="p-0">
        <Row>
          <Col id="mainNav" sm={3} xs={12} className="p-0 shadow">
            <VerticalNav />
          </Col>
          <Col id="content" sm={9} xs={12} className="p-0">
            <HorizontalNav show={this.state.showTopNav}/>
            <Header />
            <MyBackground/>
            <MyProject/>
            <MyAchievements/>
            <MyCourses/>
            <MyContact/>
          </Col>
        </Row>
      </Container>
    );
  }
}