import React, { Component } from 'react';
import { Row, Col, Image } from 'react-bootstrap';
import banner from '../../../image/banner.jpeg';

export default class Header extends Component {
  render() {
    return(
      <Row id="header" className="min-vh-100 w-100 m-0">
        <Title/>
        <MyBanner/>
      </Row>
    );
  }
}

class Title extends Component {
  render() {
    return(
    <Row id="title" className="px-sm-5 py-3 px-4 m-0 w-100" style={{zIndex: -1, background: '#e9ecef', fontWeight: 200, minHeight: '50vh'}}>
      <Col className="my-auto p-0">
        <Row className="border-bottom border-dark mb-1 mx-0" style={{fontSize: 35, lineHeight: 1.5}}>
            Mr. Chung Ka-ho Ryan <br />
            鍾嘉浩 <br />
        </Row>
        <Row className="mx-0" style={{fontSize: 18}}>
            B.Sc. in Computer Science, Year 3 (2020-2021)<br />
            The Chinese University of Hong Kong
        </Row>
      </Col>
    </Row>
    );
  }
}

class MyBanner extends Component {
  render() {
    return(
      <Row id="mybanner" className="overflow-hidden p-0 m-0 w-100" style={{zIndex: -1, maxHeight: '60vh'}}>
        <Image src={banner} className="p-0" style={{minWidth: '100%', objectFit: 'cover'}}/>
      </Row>
    );
  }
}
  