import React, { Component } from 'react';

export default class AppNav extends Component {
  ScrollToSection = (div) => {
    let scroller = window.matchMedia('(max-width: 991px)').matches ?
      { obj: window, scrollY: window.pageYOffset} : 
      { obj: document.getElementById("content"), scrollY: document.getElementById("content").scrollTop};
    let div_y_position = Math.ceil(document.getElementById(div).getBoundingClientRect().top + scroller.scrollY);
    scroller.obj.scrollTo({top: div_y_position, behavior: 'smooth'});
  }

  render() {
    return <></>;
  }
}