import { Navbar, Nav, Image } from 'react-bootstrap';
import AppNav from '../../frame/nav';
import propic from '../../../../image/propic.jpg';
import { NAV_CONFIG } from '../../../../config/config';

export default class VerticalNav extends AppNav {
  render() {
    return(
      <Navbar className="d-flex flex-column h-100 justify-content-start py-sm-2 py-4">
        <Nav className="d-flex flex-column align-items-center justify-content-start h-100">
          <Nav.Item className="px-lg-5 px-sm-3 px-5 bg-transparent">
            <Image src={propic} roundedCircle thumbnail className="ratio ratio-1x1"/>
          </Nav.Item>
          <Nav.Link className="text-dark fs-3 nav-btn pt-3 pb-2 text-center" onClick={() => this.ScrollToSection('title')}>
            Ryan Chung
          </Nav.Link>
          {
            NAV_CONFIG.components.map((component, index) => {
              return (
                <Nav.Link 
                  key={index} 
                  className="nav-btn pt-2 pb-1 text-center fs-5" 
                  onClick={() => this.ScrollToSection(component.sectionID)}
                >
                  {component.name}
                </Nav.Link>
              );
            })
          }
        </Nav>
      </Navbar>
    );
  }
}