import React, { Component } from 'react';
import Section from '../frame/section';
import { FirestoreCollection } from "@react-firebase/firestore";
import { Person } from "react-bootstrap-icons";
import Skeleton from 'react-loading-skeleton';

export default class MyBackground extends Component {
    render() {
      return(
        <Section id="mybackground" name="Background" icon={<Person className="mr-1 mb-1"/>} opacity="0.1">
          <FirestoreCollection path="/introduction/" limit={1}>
          {introduction => {
            return introduction.isLoading ? 
            <Skeleton count={10} className="mb-3"/>
            : (
              introduction.value[0].body.map((paragraph, index) => {
                return <p key={index} className="p-0">{paragraph}</p>;
              })
            );
          }}
          </FirestoreCollection>
        </Section>
      );
    }
}
  